.notify-success, .notify-error {
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
  }

  .notify-success {
    color: green;
    border-color: green;
  }

  .notify-error {
    color: red;
    border-color: red;
  }